<template>
  <div id="wrap">
    <div class="explain">车辆技改属于定制化服务，涉及的费用，需一次性支付，在首付款中进行结算。</div>
     <div class="container" style="margin-bottom:48px;">  
      <div class="des">
        <div class="mint-radiolist" v-for="(item,index) in radioArr">
          <label class="mint-radiolist-title" style="font-size:14px;">{{item.key}}</label>
          <a class="mint-cell" v-for="(radioItem, radioIndex) in item.value" @click="check()" :price="radioItem.price" :id="radioItem.id">
            <div class="mint-cell-wrapper" style="background-image:none;margin-bottom: 10px;">
              <div class="mint-cell-title">
                <label class="mint-radiolist-label">
                  <span class="mint-radio">
                    <input type="radio" class="mint-radio-input" :value= "radioItem.id">
                    <span class="mint-radio-core"></span>
                  </span>
                  <span class="mint-radio-label">{{radioItem.name}}</span>
                  <span class="mint-price">{{radioItem.price}}</span>
                  <span class="mint-unit">{{radioItem.unit}}</span>
                </label>
                <div v-if="radioItem.label" class="label-input">
                  <span>{{radioItem.label}}</span>
                  <input type="tel" style="width:75px; margin-left:5px; padding: 0 5px;" value="1" class="input" 
                          @keyup="inputFn" 
                          :class="radioItem.id">
                </div>
              </div>
            </div>
          </a>
        </div>
      </div> 
    </div>
    <div class="footer">
      <span>技改价格</span>
      <span class="price">{{vehicle.price}}</span>
      <span class="unit">{{vehicle.unit}}</span>
      <mt-button type="primary" @click="save">保存并返回</mt-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      radioArr: [],
      vehicle: {
        price: 0,
        unit: '元/辆'
      },
      checkedArr: [],
      inputArr: {}
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      document.title = '车辆技改'
    })
  },
  computed: {
    getCarInfo() {
      return this.$store.getters['GET_CAR_INFO']
    },
    getServiceInfo() {
      return this.$store.getters['GET_SERVICE_INFO']
    }
  },
  mounted() {
    if (this.$store.getters['GET_VEHICLE_INFO'].price) {
      this.vehicle.price = this.$store.getters['GET_VEHICLE_INFO'].price
    }
    var that = this
    this.$axios({
      url: this.$baseUrl + '?r=interfaces/car-valuation/get-technical-transformation-data',
      method: 'post',
      data: {
        car_type: this.getCarInfo.car_type
      },
      transformRequest: [function (data) {
        var ret = '';
        for (var it in data) {
          ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
        }
        return ret
      }],
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then(res => {
      if (res.data.errcode === 0) {
        that.radioArr = res.data.data
        that.$nextTick(function () {
          if (that.getServiceInfo.vehicleArr && that.getServiceInfo.vehicleArr.length) {
            that.getServiceInfo.vehicleArr.forEach(function (v, i) {
              $('#' + v).addClass('isCheck')
              $('#' + v).find('.mint-radio-input')[0].checked = true
              $('#' + v).find('.input')[0].value = that.getServiceInfo.vehicleinputArr[$('#' + v).find('.input')[0].className]
            })
          }
        })
      } else {
        that.$toast(res.data.msg)
      }
    })
  },
  methods: {
    check() {
      let selfPrice = 0;
      $(event.currentTarget.parentNode).find('.mint-radio-input').forEach((item) => {
        item.checked = false
      })
      $(event.currentTarget.parentNode).find('.mint-cell').removeClass('isCheck')
      $(event.currentTarget).find('.mint-radio-input')[0].checked = true
      $(event.currentTarget).addClass('isCheck')
      $('.isCheck').each(function (i, v) {
        if ($(v).find('.input').length) {
          selfPrice += $(v).attr('price') * $(v).find('.input').val() * 1
        } else {
          selfPrice += $(v).attr('price') * 1
        }
      })
      this.vehicle.price = selfPrice
    },
    inputFn() {
      let selfPrice = 0;
      let count = event.currentTarget.value
      $('.isCheck').each(function (i, v) {
        if ($(v).find('.input').length) {
          selfPrice += $(v).attr('price') * count * 1
        } else {
          selfPrice += $(v).attr('price') * 1
        }
      })
      this.vehicle.price = selfPrice
    },
    save() {
      if (!this.vehicle.price) {
        this.$toast('请选择')
        return
      }
      let that = this;
      this.$store.commit('SET_VEHICLE_INFO', this.vehicle)
      this.checkedArr = []
      this.inputArr = {}
      $('.isCheck').each(function (i, v) {
        that.checkedArr.push($(v).attr('id'))
        if ($(v).find('.input').length) {
          that.inputArr[$(v).find('.input')[0].className] = $(v).find('.input').val()
        }
      })
      this.getServiceInfo.vehicleArr = this.checkedArr
      this.getServiceInfo.vehicleinputArr = this.inputArr
      this.$router.push('./')
    }
  }
}
</script>

<style scoped>
.explain {
  font-size: 14px;
  color: #666;
  padding-bottom: 8px;
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(#d9d9d9),
    color-stop(50%, #d9d9d9),
    color-stop(50%, transparent)
  );
  background-image: linear-gradient(
    0deg,
    #d9d9d9,
    #d9d9d9 50%,
    transparent 50%
  );
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: bottom;
}
.mint-cell .mint-cell-wrapper {
  padding: 0;
}
.mint-radiolist-label {
  display: flex;
  align-items: center;
  padding: 0;
}
.mint-radiolist-label .mint-radio-label {
  flex: 1;
}
.mint-radiolist-label .mint-price {
  margin-right: 5px;
}
.label-input {
  padding: 5px 0;
  text-align: right;
  color: #a5a5a5;
}
.footer {
  position: fixed;
  bottom: 0px;
  font-size: 16px;
  width: 100%;
  line-height: 38px;
  z-index: 999;
  background-color: #fff;
  padding: 5px 0;
}
.footer .price {
  color: red;
  margin-left: 20px;
}
.footer .unit {
  margin-left: 5px;
}
.footer > button {
  border-radius: 0;
  height: 38px;
  font-size: 14px;
  float: right;
  margin-right: 20px;
  width: 120px;
}
.input {
  /* text-align: right; */
}
</style>




